	<view class="content">
		<view style="width: 750rpx;background-color: #FFFFFF;position: relative;"
			:style="'height:' + statusBarHeight + 'px;'">
			
			<view class="row" style="width: 750rpx;height: 48px;position: absolute;bottom: 0;">
				<image src="../../static/order/map-back.png" mode="aspectFit" style="margin-left: 24rpx;margin-top: 19rpx;width: 60rpx;height: 60rpx;" @click="doBack()"></image>
				<text
					style="margin-left: 166rpx;height: 48px;font-size: 18px;line-height: 48px;color: #323232;font-weight: bold;width: 250rpx;text-align: center;">评价订单</text>
					<!-- #ifdef APP-PLUS -->
					<!-- <text style="margin-left: 130rpx;height: 48px;font-size: 16px;line-height: 48px;color: #FF5725;font-weight: bold;width: 120rpx;text-align: center;" @click="doSubmit()">提交</text> -->
					<!-- #endif -->
					<!-- #ifdef MP-WEIXIN -->
					<!-- <text style="height: 48px;font-size: 16px;line-height: 48px;color: #FF5725;font-weight: bold;width: 80rpx;text-align: center;" :style="'margin-left:' + (85-menuButtonWidth) + 'px;'" @click="doSubmit()">提交</text> -->
					<!-- #endif -->
			</view>
		
		</view>
		<view class="row" style="width: 100%;height: 40px;margin-top: 10px;">
			<text style="margin-left: 12px;height: 40px;line-height: 40px;color: #323232;font-weight: bold;">配送服务</text>
			<starV size = 40 @change="onChange" :disabled = "false" :value = 0 style="margin-left: 10px;padding-top: 9px;"></starV>
			
		</view>
		<view class="textView-eva-back">
			<textarea class="textarea-eva"  :value="word" placeholder="写下对配送员的评价吧~" @input="input"/>
		</view>
		<view class="submit" @click="doSubmit()">
			提交
		</view>
	</view>
</template>

<script>
	// import starV from '../../components/rate-star/rate.vue'
	import starV from '../../components/banxing/banStar.vue'
	export default {
		components: {
			starV
		},
		data() {
			return {
				windowHeight: 0,
				windowWidth: 0,
				statusBarHeight: 0,
				orderID: '',
				menuButtonWidth: 0,
				word:'',
				star:0
			}
		},
		onLoad(option) {
			var res = uni.getSystemInfoSync()
			var that = this
			that.orderID = option.orderID
			that.windowHeight = res.windowHeight
			that.windowWidth = res.windowWidth
			that.statusBarHeight = getApp().globalData.top + 48
			that.menuButtonWidth = getApp().globalData.menuButtonWidth
			
		},
		methods: {
			doBack(){
				uni.navigateBack({
					
				})
			},
			onChange(e){
				console.log(e)
				this.star = e.value
			},
			doSubmit(){
				var that = this
				that.http.sendRequest('Orders.Evaluate',{
					'orderid':that.orderID,
					'star':that.star,
					'content':that.word
					}).then(function(res){
					if(res.code == 0){
						uni.navigateBack({
							
						})
					}
				})
			},
			input(e){
				this.word = e.detail.value
			}
		}
	}
</script>

